import React, { useEffect, useRef, useState } from 'react'
import { Layout, Menu, Breadcrumb, Button, Form, Input, message, Statistic, Row, Col, Table } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import http from '@/service' 
import moment from 'moment'
const {Countdown} = Statistic

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;



export default () => {
    const [info, setInfo] = useState({})
    const [config, setConfig] = useState({})
    const [block, setBlock] = useState([])
    const fromRef = useRef({})

    function onFinish() {
      console.log('finished!');
    }
    
    function onChange(val) {
      if (4.95 * 1000 < val && val < 5 * 1000) {
        console.log('changed!');
      }
    }

    const columns = [

      {
        title: '区块高度',
        dataIndex: 'currHeight',
        key: 'currHeight',
      },
      {
        title: '区块大小',
        dataIndex: 'blockSize',
        key: 'blockSize',
      },
      {
        title: '区块id',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: '出块时间',
        dataIndex: 'time',
        key: 'time',
        render: (v) => {
          return moment(v).format('YYYY-MM-DD HH:mm:ss')
        }
      },
      {
        title: '获得者',
        dataIndex: 'username',
        key: 'username',
      },

    ];

      const initConfig = () => {
        http({
          url: '/api/getConfig',
          method: 'post'
      }).then((res) => {
        setConfig(res.value)
      })
       }

       const initSocket = () => {
        const socket = window.io('ws://' + window.location.hostname + ':7005/home',{
          transports: ['websocket'],
        })
        // 绑定监听, 接收服务器发送的消息
        socket.on('receiveMsg', function (data) {
          console.log('客户端接收服务器发送的消息', data)
        })
        

        socket.on('init', (v) => {
          setConfig(v)
        })
        socket.on('block', (v) => {
          setBlock(v)
        })
       }

    useEffect(() => {
        http({
            url: '/api/getInfo'
        }).then((res) => {
            res.value && setInfo(res.value)

        })
        initConfig()
        initSocket()
    }, [])
    return <div>
        
        <Row gutter={16}>
    <Col span={12}>
      <Countdown title="下次出块时间" value={config.nextBlockTime} format="HH:mm:ss:SSS" />
    </Col>
    <Col span={24} style={{ marginTop: 32 }}>
      <Statistic title="区块高度" value={config.currHeight} />
    </Col>


    
  </Row>
  <h3>最近爆块</h3>
    <Table columns={columns} dataSource={block} />
    </div>
}