const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;



export default () => {
    const [info, setInfo] = useState({})
    const fromRef = useRef({})
    const onFinish = (values) => {
      window.wakuangSocket = window.io('ws://' + window.location.hostname + ':7005/wakuang',{
        transports: ['websocket'],
        query: {
          username: info.username,
          hash: values.hash
        }
      })
      setInfo()

      };
    
      const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
      };

    useEffect(() => {
        http({
            url: '/api/getInfo'
        }).then((res) => {
            res.value && setInfo(res.value)
            // fromRef.current.setFieldsValue(res.value)

        })
    }, [])
    return <div>
        <Form
      name="basic"
      labelCol={{ span: 4 }}
      wrapperCol={{ span: 16 }}
      initialValues={{ remember: true }}
      onFinish={onFinish}
      onFinishFailed={onFinishFailed}
      autoComplete="off"
      ref={(e) => {fromRef.current = e}}
    >
      {/* <Form.Item
        label="用户名"
        name="username"
      >
        <Input />
      </Form.Item>

      <Form.Item
        label="密码"
        name="password"
      >
        <Input.Password />
      </Form.Item>

      <Form.Item
        label="姓名"
        name="name"
      >
        <Input />
      </Form.Item> */}
      <Form.Item
        label="hash"
        name="hash"
      >
        <Input type="number" />
      </Form.Item>
     

      <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
        <Button type="primary" htmlType="submit"
        disabled={window.wakuangSocket}

        >
          {window.wakuangSocket ? '正在挖矿' : '开始挖矿'}
        </Button>
      </Form.Item>
    </Form>




    </div>
}