import React, { useEffect, useRef, useState } from 'react'
import { Layout, Menu, Space, Table, Button, Input, message, DatePicker, List, Comment, Avatar, Modal, Form, Row, Col, Statistic, Alert } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import http from '@/service'
import moment from 'moment'

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;



export default (props) => {


  const [info, setInfo] = useState({})
  const [transList, setTransList] = useState([])
  const [act, setAct] = useState('')
  const [currBuyBook, setCurrBuyBook] = useState({})
  const [pj, setPj] = useState('')
  const fromRef = useRef(null)
  const navigate = useNavigate()



  const getTransList = () => {
    http({
      url: '/api/transList',
      method: 'POST'
    }).then((res) => {
      setTransList(res.value)
    })

  }

  const onFinish = (v) => {
    if (!v.count || !v.address) {
      message.warn('请正确输入')
      return

    }
    if(Number.parseFloat(v.count) > Number.parseFloat(info.coin)) {
      message.warn('金额大于余额')
      return
    }
    http({
      url: '/api/trans',
      method: 'POST',
      data: v
    }).then((res) => {
      if(res.code === 0) {
        message.success('提交成功')
      } else {
        message.success('提交失败')
      }
      getTransList()
      http({
        url: '/api/getInfo'
      }).then((res) => {
        res.value && setInfo(res.value)
      })
    })
  }


  const columns = [
    {
      title: '发起用户',
      dataIndex: 'username',
      key: 'username',
    },

    {
      title: '金额',
      dataIndex: 'coin',
      key: 'coin',
    },
    {
      title: '转出地址',
      dataIndex: 'address',
      key: 'address',
    },
    {
      title: '转入地址',
      dataIndex: 'toAddress',
      key: 'toAddress',
    },


    // {
    //   title: '使用者',
    //   dataIndex: 'creater',
    //   key: 'creater',
    // },
    // {
    //   title: '注册时间',
    //   dataIndex: 'time',
    //   key: 'time',
    //   render: (v) => moment(v).format('YYYY-MM-DD hh:mm:ss')
    // },


  ];


  useEffect(() => {
    http({
      url: '/api/getInfo'
    }).then((res) => {
      res.value && setInfo(res.value)
    })

    getTransList()
  }, [])
  return <div>
    <Row gutter={16}>

      <Col span={24} style={{ marginBottom: 16 }}>
        <Statistic title="余额" value={info.coin} />
      </Col>
      <Col span={24} style={{ marginBottom: 16 }}>
        <Statistic title="钱包地址" value={info.address} groupSeparator="" />
      </Col>



    </Row>
    <p>交易</p>
    <Alert style={{marginBottom: 12}} message="请确认地址正确，否则可能导致区块丢失"></Alert>
    <Form
        name="basic"
        labelCol={{ span: 4 }}
        wrapperCol={{ span: 16 }}
        onFinish={onFinish}
        autoComplete="off"
        ref={(e) => { fromRef.current = e }}
      >
        <Form.Item
          label="目标地址"
          name="address"
          labelAlign="left"
          
        >
          <Input />
        </Form.Item>
        <Form.Item
          label="金额"
          name="count"
          labelAlign="left"
          
        >
          <Input />
        </Form.Item>
        <Form.Item wrapperCol={{ offset: 4, span: 16 }} >
        <Button type="primary" htmlType="submit">
          Submit
        </Button>
      </Form.Item>
      </Form>
    <p>交易记录</p>
    <Table pagination={{ pageSize: 5 }} columns={columns} dataSource={transList.filter(props.filter ? props.filter : (e) => true)} />

  </div>
}