import logo from './logo.svg';
import Home from './views/home/index'
import Config from './views/config/index'
import BthHome from './views/btcHome/index'
import NodeList from './views/node/index'
import UserList from './views/userList/index'
import Trans from './views/trans/index'
import LogList from './views/logList/index'
import TagList from './views/tagList/index'
import Wakuang from './views/wakuang/index'
import Login from './views/login/index'
// import EditDoc from './views/editDoc/index'
import Yonghu from './views/yonghu/index'
import Reg from './views/reg/index'
import Layout from './views/layout'
import './socket'
import './App.css';

import { HashRouter, Routes, Route, Navigate } from 'react-router-dom';
function App(){
    const LayoutHome = Layout(Home)
    const LayoutConfig = Layout(Config)
    const LayoutBthHome = Layout(BthHome)
    const LayoutNodeList = Layout(NodeList)
    const LayoutUserList = Layout(UserList)
    const LayoutTrans = Layout(Trans)
    const LayoutLogList = Layout(LogList)
    const LayoutTagList = Layout(TagList)
    const LayoutWakuang = Layout(Wakuang)
    // const LayoutEditDoc = Layout(EditDoc)
    const LayoutYonghu = Layout(Yonghu)
    return (
        <HashRouter>
            <Routes>
                <Route path='/home' element={<LayoutHome></LayoutHome>} />
                <Route path='/config' element={<LayoutConfig></LayoutConfig>} />
                <Route path='/bthHome' element={<LayoutBthHome></LayoutBthHome>} />
                <Route path='/nodeList' element={<LayoutNodeList></LayoutNodeList>} />
                <Route path='/userList' element={<LayoutUserList></LayoutUserList>} />
                <Route path='/trans' element={<LayoutTrans></LayoutTrans>} />
                <Route path='/logList' element={<LayoutLogList></LayoutLogList>} />
                <Route path='/tagList' element={<LayoutTagList></LayoutTagList>} />
                <Route path='/wakuang' element={<LayoutWakuang></LayoutWakuang>} />
                <Route path='/yonghu' element={<LayoutYonghu></LayoutYonghu>} />
                {/* <Route path='/editDoc' element={<LayoutEditDoc></LayoutEditDoc>} /> */}
                
                <Route path='/login' element={<Login></Login>} />
                <Route path='/reg' element={<Reg></Reg>} />
                <Route path="*" element={<Navigate to="/home" />} />
            </Routes>
        </HashRouter>
    )
}

export default App;
